import { createArticle } from '@/api/edit'

export default {
  middleware: 'authenticated',
  name: 'Edit',
  data() {
    return {
      article: {
        title: '',
        description: '',
        body: '',
        tagList: ''
      }
    }
  },
  methods: {
    async onSubmit() {
      await createArticle({ article: this.article })
      this.$router.push('/')
    }
  },
}
